@charset "UTF-8";
.navLink:before {
  content: "";
  display: block;
  border-top: 2px solid #000102;
  width: 8rem;
  padding-bottom: .5rem; }

.navLink:last-child:after {
  content: "";
  display: block;
  border-bottom: 2px solid #000102;
  width: 8rem;
  padding-top: .5rem; }

.navLink:hover a::before {
  display: inline-block;
  content: "•";
  margin-right: .1rem; }

.navLink:hover a::after {
  display: inline-block;
  content: "•";
  margin-left: .1rem; }
